


<template>
  <div class="mine">
    <!-- 头部 -->
    <Movie-header :type="''"></Movie-header>
    <!-- 我的 - 主体 -->
    <main class="mine-main">
      <!-- 选项 -->
      <section class="login-options">
        <ul class="login-options-ul">
          <li name="list">Login</li>
          <li name="list">Register</li>
        </ul>
      </section>
      <!-- row -->
      <section class="mine-row">
        <!-- 登陆 -->
        <section class="mine-login" name="row">
          <div class="login">
            <!-- 块 -->
            <div class="login-row">
              <section class="login-row-sl">
                <h5>username</h5>
                <input type="text" v-model="user.name" />
              </section>
            </div>
            <!-- 块 -->
            <div class="login-row">
              <section class="login-row-sl">
                <h5>password</h5>
                <input type="password" v-model="user.password" />
              </section>
            </div>
            <!-- 块 -->
            <div class="login-row">
              <section class="login-row-sl">
                <button class="submit">submit</button>
              </section>
            </div>
          </div>
        </section>
        <!-- 注册 -->
        <section class="mine-register" name="row">
          <div class="register">
            <!-- 块 -->
            <section class="register-row">
              <h5>surname</h5>
              <input type="text" v-model="register.surname" />
            </section>
            <!-- 块 -->
            <section class="register-row">
              <h5>name</h5>
              <input type="text" v-model="register.name" />
            </section>
            <!-- 块 -->
            <section class="register-row">
              <h5>email</h5>
              <input type="text" v-model="register.email" />
            </section>
            <!-- 块 -->
            <section class="register-row">
              <h5>password</h5>
              <input type="password" v-model="register.password" />
            </section>
            <!-- 注册按钮 -->
            <button class="register-buttom submit">register</button>
          </div>
        </section>
      </section>
      <!-- row - end -->
    </main>
    <!-- 脚注 - 导航 -->
    <Movie-footer></Movie-footer>
  </div>
</template>


<script>
import header from "@/components/Movie-header";
import footer from "@/components/Movie-footer";

export default {
  name: "mine",
  components: {
    "Movie-header": header,
    "Movie-footer": footer
  },
  data() {
    return {
      user: {
        name: "",
        password: ""
      },
      register: {
        surname: "", // 姓
        name: "", // 名
        email: "", // 邮箱
        password: "" // 密码
      }
    };
  },
  methods: {},
  mounted() {
    // 切换
    let list = document.getElementsByName("list");
    let row = document.getElementsByName("row");
    // 获取点击按钮
    for (let i = 0; i < list.length; i++) {
      list[i].index = i;
      list[i].onclick = function() {
        for (let j = 0; j < row.length; j++) {
          list[j].style.backgroundColor = "#fff";
          list[j].style.color = "#000";
          row[j].style.display = "none";
        }
        this.style.backgroundColor = "#ff817d";
        this.style.color = "#fff";
        row[this.index].style.display = "block";
      };
    }
  }
};
</script>

<style scoped>
/* 登陆 - 注册隐藏导航 */
.mine > .Movie-header{
  display: none;
}
.mine-main {
  padding: 4%;
  height: 95vh;
  box-sizing: border-box;
  background-color: #f5f5f5;
}

/* 选项 */
.login-options {
}
.login-options-ul {
  display: flex;
}
.login-options-ul > li {
  width: 50%;
  height: 3rem;
  background-color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.5s;
}
.login-options-ul > li:nth-child(1) {
  border-top-left-radius: 5px;
  background-color: #ff817d;
  color: #fff;
}
.login-options-ul > li:nth-child(2) {
  border-top-right-radius: 5px;
}

/* mine-row */
.mine-row {
  height: 80vh;
  background-color: #ff817d;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
}

/* 登录 */
.mine-login {
  height: 100%;
}
.login,
.register {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-content: center;
  height: 100%;
}
.login-row {
  width: 100%;
  margin: 2% 0;
}
.login-row-sl {
  width: 80%;
  text-align: left;
  margin: 0 auto;
  /* padding: 2% 0; */
}
.login-row-sl > h5,.register-row >h5 {
  font-size: 1rem;
  margin: 2% 0;
}
.login-row-sl > input,.register-row > input {
  padding: 3% 0;
  border: 3px solid #2c2c2c;
  border-radius: 3px;
  padding-left: 5px;
  width: 100%;
}
.submit {
  width: 100%;
  padding: 3%;
  background-color: #2c2c2c;
  border: 1px solid #2c2c2c;
  color: #fff;
  border-radius: 5px;
}

/* 注册 */
.mine-register {
  display: none;
  height: 100%;
}
.register-row {
  width: 40%;
  margin: 5% 2%;
  text-align: left;
}
.register-row > input{
  width: 100%;
}
.register-buttom{
  width: 50%;
  margin: 0 auto;
}

</style>

